import tw from 'twin.macro';
import styled from '@emotion/styled';
import { Link } from 'gatsby';
import BREAKPOINTS from '../../styles/breakpoints';

const LinkBase = props => {
  const { href } = props;
  const Element = href ? 'a' : Link;

  return <Element {...props} />;
};

export const NavLink = styled(LinkBase)`
  ${tw`
    first:ml-0
    mt-3 md:my-auto
    border-b-2 border-transparent hover:border-b-2 hover:border-white
    transition duration-300
  `}
  font-size: 1.25rem;
  font-weight: 500;
  line-height: 1.75rem;
  margin-left: 1.75rem;
  @media (min-width: ${BREAKPOINTS.md}px) {
    margin-left: 2.25rem;
  }
`;

export const SocialLink = styled(NavLink)`
  ${tw`hidden md:block`}
  svg {
    ${tw`hidden md:block h-5`}
    width: auto;
    height: 36px;
  }
  border: none;
  @media (min-width: ${BREAKPOINTS.md}px) {
    margin-left: 3rem;
  }
`;

export const LogoLink = styled(props => <Link {...props} />)`
  width: 311px;
  @media (min-width: ${BREAKPOINTS.md}px) {
    width: 264px;
  }
`;
